<template>
  <div class="login">
    <input type="text" placeholder="Email Address" v-model="email" />
    <input
      type="password"
      placeholder="Password"
      v-model="password"
      maxLength="6"
    />
    <button @click="login">Sign in</button>
  </div>
</template>
<script>
import { login } from 'api/user'
import { USERINFO_KEY } from '@/config'
import storage from 'assets/js/storage'
export default {
  name: '',
  data() {
    return {
      email: '',
      password: '',
    }
  },
  methods: {
    login() {
      const emailReg = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/
      if (!emailReg.test(this.email)) {
        this.$swal({
          title: 'Wrong email address',
          icon: 'error',
          buttons: false,
          timer: 1000,
        })
        return
      }
      if (this.email.trim() == '' || this.password.trim() == '') {
        this.$swal({
          title: 'Empty username or password',
          icon: 'error',
          buttons: false,
          timer: 1000,
        })
        return
      }
      const reg = /^[0-9]+$/
      if (this.password.length < 6 || !reg.test(this.password)) {
        this.$swal({
          title: 'Wrong password',
          icon: 'error',
          buttons: false,
          timer: 1000,
        })
        return
      }
      login(this.email, this.password).then((res) => {
        console.log('res', res)
        if (res && res.errno === 0) {
          // put userinfo into loacalstorage
          storage.set(USERINFO_KEY, res.data)
          this.$router.go(-1)
          return
        }
        this.$swal({
          title: 'Wrong username or password',
          icon: 'error',
          buttons: false,
          timer: 1000,
        })
      })
    },
  },
}
</script>
<style lang="scss" scoped>
@import '~assets/scss/mixins';
@media screen and (min-width: 720px) {
  .login {
    @include flex-center(column);
    input {
      width: 60%;
      height: 40px;
      padding: 0 5%;
      border-radius: 5px;
      margin-bottom: 20px;
      background-color: rgba(255, 255, 255, 0.4);
      color: #575757;
      &:nth-child(2) {
        margin-bottom: 5px;
      }
      &::placeholder {
        color: #575757;
      }
    }
    button {
      width: 40%;
      height: 40px;
      margin-top: 20px;
      border-radius: 5px;
      border: none;
      outline: none;
      background-color: rgba(255, 255, 255, 0.4);
      color: #2c3e50;
      font-size: 16px;
      font-weight: bold;
      &:hover {
        background-color: rgba(255, 255, 255, 0.5);
      }
    }
  }
}
@media screen and (max-width: 719px) {
}
</style>
